import React, { FC, useEffect, useState } from 'react'
import clsx from 'clsx'
import { Typography } from '../Typography'
import { ChipsProps, ChipStateEnum } from './types'
import './Chips.scss'

const Chips: FC<ChipsProps> = ({
  id,
  name,
  text = 'Chip',
  size = 'XXL',
  isActive = false,
  isDisabled = false,
  isOutline = false,
  isTab = false,
  onTabSelect,
  leftIcon,
  rightIcon,
  className,
}) => {
  const [state, setState] = useState(ChipStateEnum.DEFAULT)

  const handleChange = (e): void => {
    if (onTabSelect && !isDisabled) {
      onTabSelect(e)
    }
    if (!onTabSelect && state === ChipStateEnum.DEFAULT) {
      setState(ChipStateEnum.SELECTED)
    }
    if (!onTabSelect && state === ChipStateEnum.SELECTED) {
      setState(ChipStateEnum.DEFAULT)
    }
  }

  useEffect(() => {
    if (isActive) {
      setState(ChipStateEnum.SELECTED)
    }
    if (isDisabled) {
      setState(ChipStateEnum.DISABLED)
    }
    if (!isDisabled) {
      setState(ChipStateEnum.DEFAULT)
    }
  }, [isActive, isDisabled])

  return (
    <label
      className={clsx(
        'Chips',
        {
          Chips__Outline: isOutline,
          Chips__Color: !isOutline,
          '--selected': isActive,
          '--isTab': isTab,
        },
        `Chips__${size}`,
        `--${state}`,
        className,
      )}
      htmlFor={id}
    >
      <input
        checked={isActive}
        className='Chips__Input'
        id={id}
        name={name}
        onChange={(e): void => handleChange(e)}
        type='checkbox'
      />
      <div className='Chips__Body'>
        {leftIcon && leftIcon}
        <Typography
          className={clsx(
            {
              'Chips__Outline-text': isOutline,
              'Chips__Color-text': !isOutline,
              '--selected': isActive,
            },
            `Chips__${size}-text`,
            `--${state}`,
          )}
          text={text}
          type='boldBody'
        />
        {rightIcon && rightIcon}
      </div>
    </label>
  )
}

export default Chips
